.About {
    flex-grow: 1;
    padding: 250px 0 200px 0;
}

.About h2 {
    position: relative;
    text-align: center;
    font-size: 40px;
    font-weight: 600;
    margin-bottom: 60px;
    padding-bottom: 20px;
    font-size: 36px;
    text-transform: uppercase;
    color: rgb(236, 184, 7);
}

.About h2::before {
    content: "";
    position: absolute;
    bottom: 0px;
    left: 50%;
    width: 230px;
    height: 3px;
    background: #111;
    transform: translateX(-50%);
}

.About p {
    padding: 0 100px;
}